.random-planet {
  display: flex;
  align-items: center;
  padding: 1rem;
  min-height: 220px;
}

.random-planet .planet-image {
  width: 170px;
  height: 170px;
  border-radius: 10px;
  margin-right: 1rem;
}

.random-planet .list-group {
  margin: 1rem 0 0 ;
}

.random-planet .list-group-item {
  padding: .25rem;
}

.random-planet .list-group-item .term {
  margin-right: .5rem;
}